img {
  z-index: 1;
  float: bottom;
  -ms-flex-item-align: end;
      align-self: flex-end;
  position: absolute;
  right: 0px;
  bottom: 0px;
  /* background: #F5F5F5 url('./img/dracula-castles.png') no-repeat bottom right; */

}



.grid {
  display: grid;
  min-width: 200px;
  grid-template-columns: 250px auto;
  /* grid-template-columns: 2fr 1fr; */
  grid-gap: 10px;
  grid-template-areas:
    "first first"
    "second second";
}

.item {
  background-color: #111;
  color: #ace;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;

}

.item-1 {
  grid-area: first;
  /*
  grid-area: first;

  justify-content: flex-end;

  z-index: 1; */
}

.item-2 {
  display: -ms-flexbox;
  display: flex;
  grid-area: second;
}





